@media (max-width: 999px) {
    #menu {
        position: fixed;
        top: 0;
        width: 100%;
        height: 100vh;
        left: 0;
        z-index: 99999;
        overflow: scroll;

        #menu-mobile {
            background: url(/wp-content/themes/closer/img/mobi_close.png) no-repeat;
            float: right;
            width: 20px;
            height: 20px;
            margin: 20px 30px;
        }

        nav {
            padding: 50px 0 0;

            #main-menu {
                li {
                    ul {
                        padding: 20px 20px 0;

                        &:after {
                            background:url(/wp-content/themes/closer/img/arrow.png) no-repeat;
                            top: -25px;
                        }

                        li {
                            background:none;
                            padding: 0 0 10px;
                            border-bottom: 1px dashed rgba(255,255,255,0.23);
                            margin: 0 0 15px 25px;

                            &:last-child {
                                border-bottom: 0;
                            }

                            a {
                                .li_wrapper {
                                    .li_image {
                                        display: none;
                                    }

                                    .li_title_wrapper {
                                        .li_title {
                                            margin: 0;
                                            padding: 0;
                                            color: #fff;
                                        }
                                        .li_title_desc {
                                            display: none !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

    }
}

#menu_activator {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 8;
}

#main-menu {
    margin-top: 10px;
    li {

        &:hover {
            a {
                border-left-color: #f32f66 !important;
                color: #fff;
                background-color: #000;
            }
        }
        &.active {
            a {
                border-left-color: #f32f66 !important;
                color: #fff;
                background-color: #303039;
            }
        }

        a {
            padding: 10px;
            border-left: 8px solid transparent;
            color: #fff;
            line-height: 1.3;
            transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
            &:hover {
                border-left-color: #f32f66 !important;
                color: #fff;
                background-color: #000;
            }
            .li_image, .li_title_desc {
                display: none;
            }
        }
        .sub-menu {
            position: relative;
            z-index: 490;
            top: 0;
            width: 100%;
            padding: 65px 55px 65px;
            margin: 0;
            background-color: #3e3e49;
            background-color: rgba(62, 62, 73, 0.95);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#f23e3e49, endColorstr=#f23e3e49);
            -ms-filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#f23e3e49, endColorstr=#f23e3e49);
            display: none;

            @media (min-width: 999px) {
                position: absolute;
                top: 0;
                left: 200px;
                width: 780px;
                padding: 65px 55px 85px;
                height: 100vh;

            }

            &:after {
                content: "";
                background: url(/public_life_closer/wp-content/themes/closer/img/mobi_close.png) no-repeat;
                position: absolute;
                float: right;
                right: 30px;
                top: 25px;
                width: 20px;
                height: 20px;
                
            }

            &:hover {
                cursor: pointer;
            }

            li {
            	background: #fff;
            	padding: 15px 15px 15px;
            	margin: 0 0 15px;

            	&:hover {
            		background: #eff7e4;
            	}

                &.current-menu-item {
                    background: #eff7e4;
                }

                &.leading_item {
                    background: none;
                    margin-bottom: .5em;
                    font-size: 2em;

                    .li_wrapper {
                        .li_image {
                            display: none;
                        }
                        .li_title_wrapper {
                            .li_title {
                                display: inline;
                                color: #fff;
                                line-height: 1.1;
                                border-bottom: 1px solid transparent;

                                &:hover {
                                    border-bottom-color: #f32f66;
                                }
                            }
                        }
                    }
                }
                a {
                	padding: 0;
                	margin: 0;
                	border: none;
                    background:none !important;

                    display: flex;

					&:hover {
						background:none;
						border: none;
					}
                    .li_wrapper {
                        display: flex;
                        color: #959595;
                        align-items: center;

                        .li_image {
                            display: block;
                            //width: 78px;
                            margin-right: 10px;

                            img {
                            	//width: 100%;
                            	height: auto;
                            }
                        }
                        .li_title_wrapper {
                            .li_title {
                                margin-bottom: .4em;
                                color: #85c655;
                                font-weight: 400;
                                line-height: 1.1;
                            }
                            .li_title_desc {
                                font-size: .8em;
                                line-height: 1.2;
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }
}