.page_cont {
	padding: 40px;
	
	&.has_border {
		border-bottom: 1px solid #e2e0dd;
	}
}


.home {
	.latest_banner_zone {
		background: #ededed;
	}
}
.grid {
	display: grid;
}

.load_more_bt {
	&.more_rec {
		width: 100%;
		text-align: center;
		color: #f32f66;

		&:hover {
			cursor: pointer;
		}
	}
}

.post-content.event-post-content {
	float: left;
    width: 39em;
    max-width: 40em;
}

.single_event_type {
	float: left;
	width: 8em;
	padding: 0 10px 0 0;
}